.viewer div 
{ 
    visibility: hidden;
    position: fixed;
    top: 5%; right: 5%; bottom: 5%; left: 5%;
    z-index: 75;
    text-align: center;

    display: flex;
    justify-content: center;
    align-items: center;
}

.viewer div:before
{
    content: '';
    position: fixed;
    top: 0; right: 0; bottom: 0; left: 0;
    z-index: 74;
    
    background-color: rgba(0, 0, 0, 0);
    transition: all 0.5s ease-out;
}

.viewer div img
{
    position: relative;
    z-index: 77;
    max-width: 100%;
    max-height: 100%;
    
    margin-left: -9999px;
    opacity: 0;
    transition-property: all, opacity;
    transition-duration: 0.5s, 0.2s;
    transition-timing-function: ease-in-out, ease-out;
}

.viewer div:target { visibility: visible; }

.viewer div:target:before { background-color: rgba(0, 0, 0, 0.7); }

.viewer div:target img
{
    margin-left: 0px;
    opacity: 1;
}